<script lang="ts">
  import CodeMirror from "svelte-codemirror-editor";
  import { python } from "@codemirror/lang-python";
  import { ViewPlugin, EditorView } from "@codemirror/view";

  export let value: string = "";
  export let traceId: string = "";

  let hasScrolled = false;

  $: if (traceId) {
    hasScrolled = false;
  }

  function createExtensions() {
    function scrollToLinePlugin(lineNumber: number) {
      return ViewPlugin.fromClass(
        class {
          constructor(public view: EditorView) {
            this.view = view;
          }

          scroll() {
            const line = this.view.state.doc.line(lineNumber);
            const effects = EditorView.scrollIntoView(line.from, {
              y: "start",
            });
            this.view.dispatch({ effects });
          }

          update() {
            if (!hasScrolled) {
              setTimeout(() => {
                this.scroll();
                hasScrolled = true;
              }, 0);
            }
          }
        }
      );
    }

    // There tend to be imports and boilerplate at the top of
    // the generated test file, so let's scroll straight down
    return [python(), scrollToLinePlugin(20)];
  }
</script>

<CodeMirror
  bind:value
  extensions={createExtensions()}
  styles={{
    ".cm-scroller": {
      paddingBottom: "800px",
    },
  }}
  tabSize={4}
/>

