<script lang="ts">
  import { createEventDispatcher } from "svelte";

  const dispatch = createEventDispatcher<{
    buttonClick: void;
  }>();

  export let isVisible: boolean;
  export let top: number;
  export let right: number;

  const handleClick = () => {
    dispatch("buttonClick");
  };
</script>

<span
  on:click={() => {
    handleClick();
  }}
  class="toggle-button"
  style={`
    top: ${top}px;
    right: ${right}px;
  `}
>
  {isVisible ? "Hide" : "Show"} Flame Graph
</span>

<style>
  .toggle-button {
    position: absolute;
    color: var(--vscode-editor-foreground);
    opacity: 0.5;
    cursor: pointer;
    font-size: var(--vscode-editor-font-size);
  }

  .toggle-button:hover {
    opacity: 1;
  }
</style>
