export function getResolvedColor(
  color: string,
  cssVars: CSSVarsAccessor
): string {
  color = color.trim();
  if (color.startsWith("var(") && color.endsWith(")")) {
    return cssVars.get(color.slice("var(".length, -1));
  }
  return color;
}

function elementHasVScodeCssVarsSet(element: HTMLElement): boolean {
  const computedStyle = window.getComputedStyle(element);
  return computedStyle.getPropertyValue("--vscode-foreground") !== "";
}

export class CSSVarsAccessor {
  private computedStyle: CSSStyleDeclaration;
  private cache: Map<string, string> = new Map();
  constructor(window: Window) {
    if (elementHasVScodeCssVarsSet(window.document.documentElement)) {
      this.computedStyle = window.getComputedStyle(
        window.document.documentElement
      );
    } else {
      const vizElement = window.document.getElementById("viz");
      if (!vizElement) {
        throw new Error("No viz element found when trying to get CSS vars");
      }
      if (!elementHasVScodeCssVarsSet(vizElement)) {
        throw new Error("No vscode CSS vars found whenon viz element");
      }
      this.computedStyle = window.getComputedStyle(vizElement);
    }
  }
  get(varName: string): string {
    if (!this.cache.has(varName)) {
      const value = this.computedStyle.getPropertyValue(varName);
      this.cache.set(varName, value);
      return value;
    }
    return this.cache.get(varName)!;
  }
}
