<script lang="ts">
  import { onMount } from "svelte";
  import { Writable, writable } from "svelte/store";
  import { msgpackLoad, toInvocation } from "../extension/msgpack";

  import "./vscode.css";
  import "./app.css";

  import { makeExecutionTree } from "../build-frame-tree";
  import type { ExecutionTreeInfo } from "../frames-of-interest-model";
  import type { Invocation } from "../model";

  // Import from existing viz svelte
  import Viz from "../view/app/components/App.svelte";
  import { constructRootNode } from "../view/app/util/frames";
  import { CSSVarsAccessor } from "../view/app/util/css-vars";
  import type { TreeNode } from "../view/app/util/types";

  // Local WebApp imports
  import CodeMirror from "./CodeMirror.svelte";
  import Trace from "./Trace.svelte";

  interface SelectedTrace {
    tree: ExecutionTreeInfo;
    traceId: string;
  }

  const latestTraces = writable([]);
  const selectedTrace: Writable<SelectedTrace | undefined> =
    writable(undefined);

  const testCode = writable("");

  let viz: Viz | null = null; // reference to the separate Viz svelte app instance

  $: if ($selectedTrace) {
    loadVizForTrace($selectedTrace);
    generateTest($selectedTrace);
  }

  let activeView = "viz"; // default view

  function setActiveView(view: string) {
    activeView = view;
  }

  onMount(async () => {
    await loadLatestTraces();
    const unsubscribe = latestTraces.subscribe((traces) => {
      if (traces.length > 0) {
        setSelectedTrace(traces[0]);
      }
    });

    window
      .matchMedia("(prefers-color-scheme: dark)")
      .addEventListener("change", () => {
        window.dispatchEvent(
          new CustomEvent("message", { detail: { command: "themeChange" } })
        );
      });

    return () => {
      unsubscribe();
    };
  });

  async function loadLatestTraces() {
    const response = await fetch("api/latest-traces/");
    const data = await response.json();
    latestTraces.set(data.traces);
  }

  async function fetchAndParseTrace(traceId: string): Promise<Invocation> {
    const trace = await fetch(`api/traces/${traceId}/`);
    let parsedTrace;

    if (trace.headers.get("content-type") === "application/msgpack") {
      const binaryData = new Uint8Array(await trace.arrayBuffer());
      parsedTrace = toInvocation(msgpackLoad(binaryData));
    } else {
      parsedTrace = await trace.json();
    }

    return parsedTrace;
  }

  async function setSelectedTrace(traceId: string) {
    const parsedTrace = await fetchAndParseTrace(traceId);
    const tree = makeExecutionTree(parsedTrace.frames_of_interest);
    selectedTrace.set({ tree, traceId });
  }

  async function loadVizForTrace(selectedTrace: SelectedTrace) {
    if (viz) {
      viz.$destroy();
    }

    viz = new Viz({
      target: document.getElementById("viz") as HTMLElement,
      props: {
        frame: constructRootNode({
          executionTreeNodes: selectedTrace.tree.executionTreeNodes,
        }),
        cssVars: new CSSVarsAccessor(window),
        onFrameClick: (node?: TreeNode): void => {},
        onFrameFocus: (): void => {
          // do something at some point?
        },
      },
    });
  }

  async function generateTest(selectedTrace: SelectedTrace) {
    const response = await (
      await fetch(`/_kolo/api/generate-test/${selectedTrace.traceId}`)
    ).json();
    const code = response.test_code;
    testCode.set(code);
  }
</script>

<div style="display: flex">
  <div style="width: 50%;">
    <div class="m-4">
      <h1 class="text-4xl mb-4 font-bold">Kolo</h1>
      <div id="traces" class="flex flex-col">
        {#each $latestTraces as traceId}
          <Trace
            {traceId}
            onSelect={setSelectedTrace}
            {testCode}
            tree={$selectedTrace?.traceId === traceId
              ? $selectedTrace.tree
              : undefined}
            isSelected={$selectedTrace?.traceId === traceId}
          />
        {/each}
      </div>
    </div>
  </div>
  <div
    style="width: 50%; height: 100vh; border-left: 2px solid var(--vscode-textSeparator-foreground);"
  >
    <nav class="flex text-lg font-semibold">
      <div
        role="button"
        class="m-2 p-2 border rounded cursor-pointer transition-all
           {activeView === 'viz'
          ? 'bg-blue-500 text-white border-blue-700'
          : 'hover:bg-blue-100 border-transparent'}
           focus:outline-none focus:ring-2 focus:ring-blue-300 focus:ring-opacity-50"
        on:click={() => setActiveView("viz")}
        on:keydown={(event) => {
          if (event.key === "Enter") {
            setActiveView("viz");
          }
        }}
        tabindex="0"
      >
        Viz
      </div>
      <div
        role="button"
        class="m-2 p-2 border rounded cursor-pointer transition-all
           {activeView === 'test'
          ? 'bg-green-500 text-white border-green-700'
          : 'hover:bg-green-100 border-transparent'}
           focus:outline-none focus:ring-2 focus:ring-green-300 focus:ring-opacity-50"
        on:click={() => setActiveView("test")}
        on:keydown={(event) => {
          if (event.key === "Enter") {
            setActiveView("test");
          }
        }}
        tabindex="0"
      >
        Test
      </div>
    </nav>

    <div class="flex-grow">
      <div
        id="viz"
        class="{activeView === 'viz' ? 'block' : 'hidden'} h-full"
      />
      <div
        class="h-full overflow-auto {activeView === 'test'
          ? 'block'
          : 'hidden'} h-full"
      >
        <CodeMirror value={$testCode} traceId={$selectedTrace?.traceId} />
      </div>
    </div>
  </div>
</div>

<style>
  /* CSS for your component */
</style>
