import { color } from "d3-color";
import { hierarchyFromFrame } from "./frames";
import { getNodeColor, getNodeName } from "./color";
import { formatTimestamp } from "./time";
import type { FrameHierarchy, TreeNode } from "./types";
import {
  CSSVarsAccessor,
  getResolvedColor as getColorForCanvas,
} from "./css-vars";
import type { ScaleLinear } from "d3-scale";
import { getMinDecimalPlaces } from "./number";

const MIN_WIDTH_TO_DRAW_BLOCK = 0.33; // px
const MIN_WIDTH_TO_DRAW_LABEL = 10; // px
const LABEL_PADDING_LEFT = 3; // px
const LABEL_MIN_X = 5; // px

export interface FlameGraphData {
  hierarchy: FrameHierarchy;
  minStart: number;
  maxEnd: number;
  maxDepth: number;
  getStart: (node: FrameHierarchy) => number;
  getEnd: (node: FrameHierarchy) => number;
  getDepth: (node: FrameHierarchy) => number;
}

interface FlameGraphDrawParams {
  graph: FlameGraphData;
  getX: (node: FrameHierarchy) => number;
  getY: (node: FrameHierarchy) => number;
  getWidth: (node: FrameHierarchy) => number;
  getHeight: (node: FrameHierarchy) => number;
  canvasWidth: number;
  canvasHeight: number;
  context: CanvasRenderingContext2D;
  cssVars: CSSVarsAccessor;
  drawLabels?: boolean;
  xAxis?: Pick<XAxisDrawParams, "scale" | "tickHeight">;
}

interface XAxisDrawParams {
  height: number;
  scale: ScaleLinear<number, number>;
  tickLabelColor: string;
  tickLineColor: string;
  tickHeight: number;
  fontFamily: string;
  formatTick: (n: number, minDecimalPlaces?: number) => string | undefined;
  context: CanvasRenderingContext2D;
}

export function getFlameGraphData(frame: TreeNode): FlameGraphData {
  const hierarchy = hierarchyFromFrame(frame);

  let maxDepth: number = 0;
  let minTime: number = +Infinity;
  let maxTime: number = -Infinity;

  visitNodes(hierarchy, (node) => {
    const depth = node.depth;
    if (maxDepth < depth) {
      maxDepth = depth;
    }
    const { start, end } = node.data.timing;
    if (minTime > start) {
      minTime = start;
    }
    if (maxTime < end) {
      maxTime = end;
    }
  });

  const getStart = (node: FrameHierarchy) => node.data.timing.start - minTime;
  const getEnd = (node: FrameHierarchy) => node.data.timing.end - minTime;
  const getDepth = (node: FrameHierarchy) => node.depth;

  return {
    hierarchy,
    minStart: 0,
    maxEnd: maxTime - minTime,
    maxDepth,
    getStart,
    getEnd,
    getDepth,
  };
}

export function drawFlameGraph({
  graph,
  getX,
  getY,
  getWidth,
  getHeight,
  canvasWidth,
  canvasHeight,
  context,
  cssVars,
  drawLabels = true,
  xAxis = undefined,
}: FlameGraphDrawParams): void {
  context.save();

  const backgroundColor = cssVars.get("--vscode-editor-background");
  const foregroundColor = cssVars.get("--vscode-editor-foreground");
  const labelColor = setOpacity(backgroundColor, 0.9);
  const sublabelColor = setOpacity(backgroundColor, 0.55);

  // Set the font once
  const fontWeight = cssVars.get("--vscode-font-weight");
  const fontFamily = cssVars.get("--vscode-font-family");
  context.font = `${fontWeight} 11px ${fontFamily}`;

  visitNodes(graph.hierarchy, (node) => {
    // Calculate coordinates
    const x = getX(node);
    const y = getY(node);
    const width = getWidth(node);
    const height = getHeight(node);

    // Do not draw nodes outside viewport, or too small
    if (
      x + width < 0 ||
      x > canvasWidth ||
      y + height < 0 ||
      y > canvasHeight ||
      width < MIN_WIDTH_TO_DRAW_BLOCK
    ) {
      return;
    }

    // Draw the blocks
    const fillColor =
      color(getColorForCanvas(getNodeColor(node.data), cssVars)) ??
      color("red")!;
    fillColor.opacity = 0.6;
    context.fillStyle = fillColor.formatRgb();
    context.fillRect(x, y, width, height);
    context.strokeStyle = backgroundColor;
    context.lineWidth = 0.2;
    context.strokeRect(x, y, width, height);

    // Draw the label
    if (drawLabels && width >= MIN_WIDTH_TO_DRAW_LABEL) {
      context.save();
      context.rect(x, y, width, height);
      context.clip();
      context.fillStyle = labelColor;
      const label = getNodeName(node.data);
      const labelX = Math.max(LABEL_MIN_X, x + LABEL_PADDING_LEFT);
      const labelY = y + height - 4;
      context.fillText(label, labelX, labelY);
      const labelWidth = context.measureText(label).width;
      // sublabel
      const sublabel = formatTimestamp(node.data.timing.total);
      context.fillStyle = sublabelColor;
      context.fillText(`(${sublabel})`, labelX + labelWidth + 3, labelY);
      context.restore();
    }
  });

  if (xAxis) {
    // add a top fade when axis is shown
    const gradient = context.createLinearGradient(0, 0, 0, xAxis.tickHeight);
    gradient.addColorStop(0, backgroundColor);
    gradient.addColorStop(0.2, setOpacity(backgroundColor, 0.9));
    gradient.addColorStop(1.0, setOpacity(backgroundColor, 0.6));
    context.fillStyle = gradient;
    context.fillRect(0, 0, canvasWidth, xAxis.tickHeight);

    drawXAxis({
      height: canvasHeight,
      scale: xAxis.scale,
      formatTick: formatTimestamp,
      fontFamily: fontFamily,
      tickHeight: xAxis.tickHeight,
      tickLabelColor: setOpacity(foregroundColor, 0.8),
      tickLineColor: setOpacity(foregroundColor, 0.15),
      context,
    });
  }

  context.restore();
}

function drawXAxis({
  height,
  scale,
  formatTick: format,
  fontFamily,
  tickLabelColor,
  tickLineColor,
  tickHeight,
  context,
}: XAxisDrawParams): void {
  const tickFontSize = 11;
  const roughTickDistancePx = Math.round(9 * tickFontSize);
  const totalTicks: number =
    Math.abs(scale.range()[0] - scale.range()[1]) / roughTickDistancePx;
  const tickValues: number[] = scale.ticks(totalTicks);
  const minDecimalPlaces: number | undefined = getMinDecimalPlaces(tickValues);

  context.save();

  context.font = `${tickFontSize}px ${fontFamily}`;
  context.fillStyle = tickLabelColor;
  context.strokeStyle = tickLineColor;
  context.lineWidth = 1;

  for (const value of tickValues) {
    const position = scale(value);
    const label = format(value, minDecimalPlaces);

    // draw line
    context.beginPath();
    context.moveTo(position, tickHeight);
    context.lineTo(position, height);
    context.stroke();

    // draw text
    if (label) {
      context.textAlign = "center";
      context.fillText(label, position, tickHeight * 0.75);
    }
  }

  context.restore();
}

export function drawHorizontalShadow({
  x,
  y,
  width,
  height,
  color,
  orient,
  context,
}: {
  x: number;
  y: number;
  width: number;
  height: number;
  color: string;
  orient: "left" | "right";
  context: CanvasRenderingContext2D;
}) {
  const gradient =
    orient === "left"
      ? context.createLinearGradient(x, y, x + width, y)
      : context.createLinearGradient(x + width, y, x, y);
  gradient.addColorStop(0.0, setOpacity(color, 0.9));
  gradient.addColorStop(0.5, setOpacity(color, 0.3));
  gradient.addColorStop(0.75, setOpacity(color, 0.1));
  gradient.addColorStop(1.0, setOpacity(color, 0.0));
  context.fillStyle = gradient;
  context.fillRect(x, y, width, height);
}

export function setOpacity(c: string, opacity: number): string {
  const d3color = color(c)!;
  d3color.opacity = opacity;
  return d3color?.formatRgb();
}

export function visitNodes(
  frame: FrameHierarchy,
  fn: (frame: FrameHierarchy) => void,
  stop?: (frame: FrameHierarchy) => boolean
): void {
  if (stop && stop(frame)) {
    return;
  }
  if (frame.data.type !== "root") {
    fn(frame);
  }
  frame.children?.forEach((frame) => visitNodes(frame, fn, stop));
}

export function binarySearch<T>(
  array: T[],
  comparator: (node: T) => -1 | 0 | 1
): T | undefined {
  let left = 0;
  let right = array.length - 1;

  while (left <= right) {
    const mid = Math.floor((left + right) / 2);
    const element = array[mid];
    const compareResult = comparator(element);

    if (compareResult === 0) {
      return element;
    } else if (compareResult < 0) {
      right = mid - 1;
    } else {
      left = mid + 1;
    }
  }

  return undefined; // Target not found
}
