<script context="module">export {};
</script>

<script>import { createEventDispatcher, onDestroy, onMount } from "svelte";
import { basicSetup } from "codemirror";
import { EditorView, keymap, placeholder as placeholderExt } from "@codemirror/view";
import { EditorState, StateEffect } from "@codemirror/state";
import { indentWithTab } from "@codemirror/commands";
import { indentUnit } from "@codemirror/language";
import { debounce } from "./util";
let classes = "";
export { classes as class };
export let value = "";
export let basic = true;
export let lang = undefined;
export let theme = undefined;
export let extensions = [];
export let useTab = true;
export let tabSize = 2;
export let styles = undefined;
export let lineWrapping = false;
export let editable = true;
export let readonly = false;
export let placeholder = undefined;
const is_browser = typeof window !== "undefined";
const dispatch = createEventDispatcher();
let element;
let view;
let update_from_prop = false;
let update_from_state = false;
let first_config = true;
let first_update = true;
$: state_extensions = [
    ...get_base_extensions(basic, useTab, tabSize, lineWrapping, placeholder, editable, readonly, lang),
    ...get_theme(theme, styles),
    ...extensions,
];
$: view && update(value);
$: view && state_extensions && reconfigure();
onMount(() => (view = create_editor_view()));
onDestroy(() => view?.destroy());
function create_editor_view() {
    const on_change = debounce(handle_change, 300);
    return new EditorView({
        parent: element,
        state: create_editor_state(value),
        dispatch(transaction) {
            view.update([transaction]);
            if (!update_from_prop && transaction.docChanged) {
                on_change();
            }
        },
    });
}
function reconfigure() {
    if (first_config) {
        first_config = false;
        return;
    }
    view.dispatch({
        effects: StateEffect.reconfigure.of(state_extensions),
    });
}
function update(value) {
    if (first_update) {
        first_update = false;
        return;
    }
    if (update_from_state) {
        update_from_state = false;
        return;
    }
    update_from_prop = true;
    view.setState(create_editor_state(value));
    update_from_prop = false;
}
function handle_change() {
    const new_value = view.state.doc.toString();
    if (new_value === value)
        return;
    update_from_state = true;
    value = new_value;
    dispatch("change", value);
}
function create_editor_state(value) {
    return EditorState.create({
        doc: value ?? undefined,
        extensions: state_extensions,
    });
}
function get_base_extensions(basic, useTab, tabSize, lineWrapping, placeholder, editable, readonly, lang) {
    const extensions = [
        indentUnit.of(" ".repeat(tabSize)),
        EditorView.editable.of(editable),
        EditorState.readOnly.of(readonly),
    ];
    if (basic)
        extensions.push(basicSetup);
    if (useTab)
        extensions.push(keymap.of([indentWithTab]));
    if (placeholder)
        extensions.push(placeholderExt(placeholder));
    if (lang)
        extensions.push(lang);
    if (lineWrapping)
        extensions.push(EditorView.lineWrapping);
    return extensions;
}
function get_theme(theme, styles) {
    const extensions = [];
    if (styles)
        extensions.push(EditorView.theme(styles));
    if (theme)
        extensions.push(theme);
    return extensions;
}
</script>

{#if is_browser}
    <div class="codemirror-wrapper {classes}" bind:this={element} />
{:else}
    <div class="scm-waiting {classes}">
        <div class="scm-waiting__loading scm-loading">
            <div class="scm-loading__spinner" />
            <p class="scm-loading__text">Loading editor...</p>
        </div>

        <pre class="scm-pre cm-editor">{value}</pre>
    </div>
{/if}

<style>
    .codemirror-wrapper :global(.cm-focused) {
        outline: none;
    }

    .scm-waiting {
        position: relative;
    }
    .scm-waiting__loading {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: rgba(255, 255, 255, 0.5);
    }

    .scm-loading {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .scm-loading__spinner {
        width: 1rem;
        height: 1rem;
        border-radius: 100%;
        border: solid 2px #000;
        border-top-color: transparent;
        margin-right: 0.75rem;
        animation: spin 1s linear infinite;
    }
    .scm-loading__text {
        font-family: sans-serif;
    }
    .scm-pre {
        font-size: 0.85rem;
        font-family: monospace;
        tab-size: 2;
        -moz-tab-size: 2;
        resize: none;
        pointer-events: none;
        user-select: none;
        overflow: auto;
    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
</style>
