<script lang="ts">
  import type { ExecutionTreeInfo } from "../frames-of-interest-model";
  import type { Writable } from "svelte/store";

  export let traceId: string;
  export let onSelect: (id: string) => void;
  export let isSelected: boolean;
  export let tree: ExecutionTreeInfo | undefined;
  export let testCode: Writable<string>;

  // TODO:
  // The raw trace should be passed in here. Then we make the tree in here
  // while still having access to the rest of the raw trace. One for tomorrow.

  // Generalise this with ths vscode code.
  // How much work would it be to make a generic
  // shared ProcessedTrace. Feels doable.
  function isSimpleServedRequest(tree: ExecutionTreeInfo): boolean {
    return (
      tree.executionTreeNodes.length === 1 &&
      tree.executionTreeNodes[0].type === "nested_served_http_request"
    );
  }

  function traceName(tree: ExecutionTreeInfo): string {
    const firstNode = tree.executionTreeNodes[0];
    if (isSimpleServedRequest(tree)) {
      const request = firstNode.data.request;
      return `${request.method} ${request.path_info}`;
    }

    // TODO formattedCommand logic is missing here atm.

    const coreLabel = `${firstNode.name} and ${
      tree.totalExecutionTreeNodeCount - 1
    } other calls`;

    return coreLabel;
  }
</script>

<div
  role="button"
  class="trace my-1 p-2 border-2 rounded-md cursor-pointer {isSelected
    ? 'border-gray-500 border-4'
    : 'border-gray-300'}"
  tabindex="0"
  on:click={() => onSelect(traceId)}
  on:keydown={(event) => {
    if (event.key === "Enter" || event.key === " ") {
      onSelect(traceId);
    }
  }}
>
  {#if tree}
    {traceName(tree)}
  {:else}
    {traceId}
  {/if}
</div>
