import { min, pairs } from "d3-array";
import { format } from "d3-format";

/**
 *  Computes the base-10 magnitude of a number, which can be useful for rounding by sigfigs etc.
 * Formally, numberMagnitude computes `m` such that `10^(m-1) <= abs(num) < 10^m`.
 * Equivalently, `num / 10^(numberMagnitude(num))` is always in the range ±[0.1, 1[.
 *
 * - numberMagnitude(0.5) = 0
 * - numberMagnitude(1) = 1
 * - numberMagnitude(-2) = 1
 * - numberMagnitude(100) = 3
 */
export function getMagnitude(num: number): number {
  if (num === 0) {
    return 0;
  }
  const magnitude = Math.floor(Math.log10(Math.abs(num))) + 1;
  return Number.isFinite(magnitude) ? magnitude : 0;
}

export function getMinDecimalPlaces(ticks: number[]): number | undefined {
  const minDist = min(pairs(ticks, (a, b) => Math.abs(a - b)));
  if (minDist !== undefined) {
    // Find the decimal places required to reach the first non-zero digit
    // We are multiplying by 1.000001 to compensate for floating point error.
    // If we err on the side of being greater, then magnitude is _more_ correct.
    return -getMagnitude(minDist * 1.000001) + 1;
  }
  return undefined;
}

interface NumberFormattingOptions {
  /** number of decimal places */
  dp?: number;
  suffix?: string;
}

export function formatNumber(
  v: number,
  options: NumberFormattingOptions = {}
): string {
  let formatted: string = v.toString();
  if (options.dp !== undefined) {
    formatted = format(`,.${options.dp}f`)(v);
  }
  if (options.suffix !== undefined) {
    formatted += options.suffix;
  }
  return formatted;
}
