import { formatNumber } from "./number";

export function formatTimestamp(
  seconds: number,
  minDecimalPlaces: number = 0
): string {
  if (seconds === 0) {
    return "0";
  }
  if (seconds < 1) {
    return formatNumber(seconds * 1e3, {
      suffix: "ms",
      dp: Math.max(0, minDecimalPlaces - 3),
    });
  }
  if (seconds < 60) {
    return formatNumber(seconds, {
      suffix: "s",
      dp: Math.max(0, minDecimalPlaces),
    });
  }
  return formatNumber(seconds / 60, {
    suffix: "m",
    dp: Math.max(0, minDecimalPlaces + 1),
  });
}
