<script lang="ts">
  import FrameTree from "./FrameTree.svelte";
  import type { TreeNode } from "../util/types";
  import FlameGraph from "./FlameGraph.svelte";
  import FlameGraphToggleButton from "./FlameGraphToggleButton.svelte";
  import HorizontalSeparator from "./HorizontalSeparator.svelte";
  import type { CSSVarsAccessor } from "../util/css-vars";

  export let frame: TreeNode;
  export let cssVars: CSSVarsAccessor;
  export let focusFrame: TreeNode | undefined = undefined;

  export let onFrameClick: (frame?: TreeNode) => void;
  export let onFrameFocus: (frame?: TreeNode) => void;

  let isFlameGraphVisible: boolean = true;

  let windowWidth: number | undefined;
  let windowHeight: number | undefined;
  let topPaneHeight: number | undefined;

  $: if (windowHeight)
    topPaneHeight = isFlameGraphVisible ? windowHeight * 0.7 : windowHeight;

  function onFrameFocusInternal(event: CustomEvent<TreeNode | undefined>) {
    // Svelte doesn't preserve `undefined` when passed as event data,
    // instead it converts it to `null`.
    const frame = event.detail ?? undefined;
    focusFrame = frame;
    onFrameFocus(frame);
  }
</script>

<main bind:clientWidth={windowWidth} bind:clientHeight={windowHeight}>
  {#if windowWidth && windowHeight && topPaneHeight}
    <FrameTree
      {frame}
      {focusFrame}
      width={windowWidth}
      height={topPaneHeight}
      on:frameClick={(event) => onFrameClick(event.detail)}
      on:frameFocus={onFrameFocusInternal}
    />

    <FlameGraphToggleButton
      isVisible={isFlameGraphVisible}
      top={topPaneHeight - 24}
      right={16}
      on:buttonClick={() => {
        isFlameGraphVisible = !isFlameGraphVisible;
      }}
    />

    {#if isFlameGraphVisible}
      <HorizontalSeparator y={topPaneHeight} />

      <FlameGraph
        {frame}
        {focusFrame}
        {cssVars}
        width={windowWidth}
        height={windowHeight - topPaneHeight}
        on:frameClick={(event) => onFrameClick(event.detail)}
        on:frameFocus={onFrameFocusInternal}
      />
    {/if}
  {/if}
</main>

<style>
  :global(body),
  :global(html),
  main {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
  }
</style>
