<script lang="ts">
  import type { ScaleLinear } from "d3-scale";
  import type { ZoomTransform, D3ZoomEvent, ZoomBehavior } from "d3-zoom";
  import { scaleLinear } from "d3-scale";
  import { pointer, select } from "d3-selection";
  import { zoom, zoomIdentity } from "d3-zoom";
  import { createEventDispatcher, onMount, tick } from "svelte";

  import type { FrameHierarchy, TreeNode } from "../util/types";
  import type { CSSVarsAccessor } from "../util/css-vars";
  import {
    type FlameGraphData,
    getFlameGraphData,
    drawFlameGraph,
    binarySearch,
    drawHorizontalShadow,
  } from "../util/flame-graph";

  const FRAME_HEIGHT = 15;
  const FRAME_PADDING = 1;
  const AXIS_TICK_HEIGHT = 18;

  export let frame: TreeNode;
  export let width: number;
  export let height: number;
  export let cssVars: CSSVarsAccessor;
  export let focusFrame: TreeNode | undefined = undefined;

  let graph: FlameGraphData;
  $: graph = getFlameGraphData(frame);

  let vizHeight: number = 0;
  $: vizHeight = (FRAME_HEIGHT + FRAME_PADDING) * (graph.maxDepth - 1);

  let focusNode: FrameHierarchy | undefined;
  $: focusNode = graph.hierarchy.find((node) => node.data === focusFrame);

  const margin = { top: AXIS_TICK_HEIGHT + 2, right: 8, bottom: 10, left: 8 };

  let xScale: ScaleLinear<number, number>;
  $: xScale = scaleLinear()
    .domain([graph.minStart, graph.maxEnd])
    .range([margin.left, width - margin.right]);

  let yScale: (depth: number) => number;
  $: yScale = (depth) =>
    (depth - 1) * (FRAME_HEIGHT + FRAME_PADDING) + margin.top + zoomTransform.y;

  let yScaleInvert: (y: number) => number;
  $: yScaleInvert = (y) =>
    1 +
    Math.floor(
      (y - margin.top - zoomTransform.y) / (FRAME_HEIGHT + FRAME_PADDING)
    );

  let zoomTransform: ZoomTransform = zoomIdentity;

  let xScaleZoomed: ScaleLinear<number, number>;
  $: xScaleZoomed = zoomTransform.rescaleX(xScale);

  let zoomViewport: [[number, number], [number, number]];
  $: zoomViewport = [
    [margin.left, margin.top],
    [width - margin.right, height - margin.bottom],
  ];

  let zoomTranslateExtent: [[number, number], [number, number]];
  $: zoomTranslateExtent = [
    [margin.left, margin.top],
    [
      width - margin.right,
      Math.max(margin.top + vizHeight + margin.bottom, height - margin.bottom),
    ],
  ];

  let zoomBehaviour: ZoomBehavior<Element, unknown> = zoom();
  $: zoomBehaviour = zoom()
    .extent(zoomViewport)
    .translateExtent(zoomTranslateExtent)
    .constrain(constrain)
    // On *super* large scale values, the blocks start to disappear.
    // I haven't figured out why it's happening, this is a quick and
    // dirty fix.
    .scaleExtent([1, 2e4])
    .on("zoom", onZoom);

  let canvas: HTMLCanvasElement;

  // If `zoomBehavour` changes, we want to re-attach it to the root element.
  $: if (canvas) select(canvas as Element).call(zoomBehaviour);

  // When resizing, re-center the zoom, redraw the canvas
  let prevWidth: number | undefined;
  let prevHeight: number | undefined;
  $: if (width !== prevWidth || height !== prevHeight) {
    if (prevWidth !== undefined && prevHeight !== undefined) {
      zoomTransform = constrain(
        zoomIdentity
          .scale(zoomTransform.k)
          .translate(
            ((zoomTransform.x / prevWidth) * width) / zoomTransform.k,
            zoomTransform.y / zoomTransform.k
          ),
        zoomViewport,
        zoomTranslateExtent
      );
      setZoomAttribute(zoomTransform);
    }
    prevWidth = width;
    prevHeight = height;

    // Redraw on resize.
    // We need to wait a tick() for attributes to be applied to the DOM.
    tick().then(() => drawCanvas());
  }

  // Redraw the canvas when CSS vars change
  $: if (cssVars) drawCanvas();

  function setZoomAttribute(t: ZoomTransform): void {
    select(canvas as Element).property("__zoom", t);
  }

  // Create a custom `constrain` function which doesn't apply scale vertically.
  // See: https://github.com/d3/d3-zoom#zoom_constrain
  function constrain(
    transform: ZoomTransform,
    extent: [[number, number], [number, number]],
    translateExtent: [[number, number], [number, number]]
  ): ZoomTransform {
    const dx0 = transform.invertX(extent[0][0]) - translateExtent[0][0],
      dx1 = transform.invertX(extent[1][0]) - translateExtent[1][0],
      dy0 =
        transform.invertY(extent[0][1]) - translateExtent[0][1] / transform.k,
      dy1 =
        transform.invertY(extent[1][1]) - translateExtent[1][1] / transform.k;
    return transform.translate(
      dx1 > dx0 ? (dx0 + dx1) / 2 : Math.min(0, dx0) || Math.max(0, dx1),
      dy1 > dy0 ? (dy0 + dy1) / 2 : Math.min(0, dy0) || Math.max(0, dy1)
    );
  }

  async function onZoom(this: Element, event: D3ZoomEvent<Element, unknown>) {
    const updateY =
      event.transform.k === zoomTransform.k &&
      event.sourceEvent?.type !== "wheel" &&
      event.sourceEvent?.type !== "dblclick";
    zoomTransform = constrain(
      zoomIdentity
        .translate(
          event.transform.x,
          // Do not update Y translation if we are zooming
          updateY ? event.transform.y : zoomTransform.y
        )
        .scale(event.transform.k),
      zoomViewport,
      zoomTranslateExtent
    );
    // Override the D3 zoom transform for the element.
    // Since we apply a custom constraint above, if we don't do this, D3's
    // source of truth and our applied transform will be out of sync.
    setZoomAttribute(zoomTransform);
    // Redraw on zoom change
    await tick();
    drawCanvas();
  }

  const dispatch = createEventDispatcher<{
    frameClick: TreeNode | undefined;
    frameFocus: TreeNode | undefined;
  }>();

  function onFocus(node: FrameHierarchy) {
    dispatch("frameFocus", node.data);
  }

  function onBlur() {
    dispatch("frameFocus", undefined);
  }

  function onClick(node: FrameHierarchy | undefined) {
    dispatch("frameClick", node?.data);
  }

  function findNodeFromCoords(
    mouseX: number,
    mouseY: number
  ): FrameHierarchy | undefined {
    const mouseDepth = yScaleInvert(mouseY);
    if (mouseDepth < 1 || mouseDepth > graph.maxDepth) {
      // do nothing
      return undefined;
    }
    let current: FrameHierarchy | undefined = graph.hierarchy;
    while (current !== undefined && current.depth < mouseDepth) {
      current = binarySearch(current.children ?? [], (node) => {
        const x = getX(node);
        const width = getWidth(node);
        if (mouseX >= x && mouseX <= x + width) {
          return 0;
        } else if (mouseX < x) {
          return -1;
        } else {
          return 1;
        }
      });
    }
    return current;
  }

  function onMouseMove(event: MouseEvent) {
    const [x, y] = pointer(event);
    const hoverNode = findNodeFromCoords(x, y);
    if (hoverNode) {
      if (hoverNode !== focusNode) {
        onFocus(hoverNode);
      }
    } else {
      onBlur();
    }
  }

  function onFrameClick(event: MouseEvent) {
    const [x, y] = pointer(event);
    const clickNode = findNodeFromCoords(x, y);
    if (clickNode) {
      onClick(clickNode);
    }
  }

  // The ratio of physical pixels to CSS pixels. We need this to draw high-resolution in canvas.
  let devicePixelRatio: number;

  // NOTE: the function definitions below are intentionally inline.
  // If they are defined elsewhere, Svelte isn't smart enough to
  // know which variables to react to (dependencies are determined
  // at compile time).

  let getX: (node: FrameHierarchy) => number;
  $: getX = (node) => xScaleZoomed(graph.getStart(node));

  let getY: (node: FrameHierarchy) => number;
  $: getY = (node) => yScale(graph.getDepth(node));

  let getWidth: (node: FrameHierarchy) => number;
  $: getWidth = (node) =>
    xScaleZoomed(graph.getEnd(node)) - xScaleZoomed(graph.getStart(node));

  let getHeight: (node: FrameHierarchy) => number;
  $: getHeight = () => FRAME_HEIGHT;

  // Erases and redraws the entire visualisation (everything inside <canvas> only).
  // There are parts (like focus outline) that are HTML and reactively redrawn by Svelte.
  function drawCanvas() {
    // If there is no canvas, we are not finished loading.
    if (!canvas) return;

    // Set width & height first because changing
    // them erases the canvas.
    canvas.width = width * devicePixelRatio;
    canvas.height = height * devicePixelRatio;

    const context = canvas.getContext("2d")!;

    // Erase the canvas (the resize above should erase it, but just in case)
    context.clearRect(0, 0, canvas.width, canvas.height);

    // High-resoluton screens require drawing multiple pixels per CSS pixel
    context.scale(devicePixelRatio, devicePixelRatio);
    context.save();

    // Draw the visualisation
    drawFlameGraph({
      graph,
      getX,
      getY,
      getWidth,
      getHeight,
      canvasWidth: width,
      canvasHeight: height,
      context,
      cssVars,
      xAxis: {
        scale: xScaleZoomed,
        tickHeight: AXIS_TICK_HEIGHT,
      },
    });

    // Draw shadows at the edges if the user has panned
    const shadowActivationThreshold = 70; // px
    const shadowMaxSize = 20; // px
    const shadowColor = cssVars.get("--vscode-editor-background");
    // left edge
    const leftEdge = -margin.left * (zoomTransform.k - 1);
    const leftOffset = Math.abs(zoomTransform.x - leftEdge);
    if (leftOffset > 0) {
      const coefficient = Math.min(leftOffset / shadowActivationThreshold, 1.0);
      const shadowSize = shadowMaxSize * coefficient;
      drawHorizontalShadow({
        x: 0,
        y: 0,
        width: shadowSize,
        height,
        color: shadowColor,
        orient: "left",
        context,
      });
    }
    // right edge
    const rightEdge = (-width + margin.right) * (zoomTransform.k - 1);
    const rightOffset = Math.abs(rightEdge - zoomTransform.x);
    if (rightOffset > 0) {
      const coefficient = Math.min(
        rightOffset / shadowActivationThreshold,
        1.0
      );
      const shadowSize = shadowMaxSize * coefficient;
      drawHorizontalShadow({
        x: width - shadowSize,
        y: 0,
        width: shadowSize,
        height,
        color: shadowColor,
        orient: "right",
        context,
      });
    }

    context.restore();
  }

  onMount(() => {
    // Draw on first load
    drawCanvas();
  });
</script>

<svelte:window bind:devicePixelRatio />

<main>
  <div class="container" style={`cursor: ${focusNode ? "pointer" : "default"}`}>
    <canvas
      bind:this={canvas}
      tabindex="0"
      aria-label="Flame Chart"
      role="tree"
      style={`width: ${width}px; height: ${height}px;`}
      on:mousemove={onMouseMove}
      on:click={onFrameClick}
    />
    {#if focusNode}
      <div
        class="focus-marker"
        style={`left: ${getX(focusNode) - 1}px; top: ${
          getY(focusNode) - 1
        }px; width: ${getWidth(focusNode) + 2}px; height: ${
          getHeight(focusNode) + 2
        }px`}
      />
    {/if}
  </div>
</main>

<style>
  .container {
    position: relative;
    overflow: hidden; /* the focus marker shouldn't be visible outside container */
  }
  .container canvas {
    display: block;
  }
  .focus-marker {
    position: absolute;
    box-shadow: 0 0 0 3px var(--vscode-list-focusOutline);
    opacity: 0.8;
    pointer-events: none;
  }
</style>
